h1 {
  text-align: center;
  color: #2b2d42;
  margin-top: 2rem;
}

.bk-banner {
  position: absolute;
  top: 0;
  background-color: #2b2d42;
  height: 20rem;
  width: 100%;
  /* The other z-index is on the .grid-container */
  z-index: -1;
}

.app-box {
  position: relative;
  height: 14.563rem;
  width: 100%;
  padding: 1rem;
}

.search-container {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
}

.search {
  border: none;
  outline-color: #80808080;
  border: solid 1px gray;
  padding: 0.4rem;
  color: #808080;
  display: flex;
  border-radius: 0.2rem;
  width: 100%;
}

.search:focus {
  outline: none;
}

h2 {
  color: #5c728a;
  margin: 1rem 0;
  font-size: 1.8rem;
}

.scroll-btn {
  position: fixed;
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  border: none;
  color: #fafafa;
  background-color: #2b2d42;
  bottom: 3rem;
  right: 2rem;
  opacity: 1;
  transition: opacity 1s ease;
  cursor: pointer;
}

.scroll-btn-hidden {
  visibility: hidden;
  opacity: 0;
  transition: opacity 1s ease;
}

.img-btn-up {
  margin: 0 auto;
  max-height: 50%;
  cursor: pointer;
}